}*/

.markdown-body h3 {
  font-size: 1.5em;
  line-height: 1.43;
}
.markdown-body h4 {
  font-size: 1.25em;
}
.markdown-body h5 {
  font-size: 1em;
}
.markdown-body h6 {
  font-size: 1em;
  color: #777;
}
.markdown-body p,
.markdown-body blockquote,
.markdown-body ul,
.markdown-body ol,
.markdown-body dl,
.markdown-body table {
  margin: 0.8em 0;
}
.markdown-body li > ol,
.markdown-body li > ul {
  margin: 0 0;
  padding-top: 0px;
  padding-bottom: 0px;
}
.markdown-body ul {
  display: block;
  align-items: start;
  text-align: left;
  justify-content: start;
  list-style-type: disc;
}
.markdown-body ul ul {
  list-style-type: circle;
}
.markdown-body ul ul ul {
  list-style-type: square;
}
.markdown-body li {
  display: list-item;
  align-items: start;
  text-align: left;
  justify-content: start;
  margin-left: 0;
}
.markdown-body hr {
  height: 2px;
  padding: 0;
  margin: 16px 0;
  background-color: #e7e7e7;
  border: 0 none;
  overflow: hidden;
  box-sizing: content-box;
}
.markdown-body ol,
.markdown-body ul,
.markdown-body li {
  line-height: 2;
}
.markdown-body li p.first {
  display: inline-block;
  margin: 0;
  padding: 0;
}
.markdown-body li p {
  margin: 0px;
  padding-top: 0px;
  padding-bottom: 0px;
}
.markdown-body ol {
  margin: 0;
  padding-top: 0px;
  padding-bottom: 0px;
}
.markdown-body ul {
  margin: 0;
  padding-top: 0px;
  padding-bottom: 0px;
}
.markdown-body [type="checkbox"],
.markdown-body [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

.markdown-body ul,
.markdown-body ol {
  padding-left: 30px;
}
.markdown-body ul:first-child,
.markdown-body ol:first-child {
  margin-top: 0;
}
.markdown-body ul:last-child,
.markdown-body ol:last-child {
  margin-bottom: 0;
}
.markdown-body ul > li {
  margin: 0;
  padding: 0;
}
.markdown-body li > ul {
  margin-top: 0;
  padding-top: 0;
}
.markdown-body .contains-task-list ul {
  margin-top: 0;
}
.markdown-body .contains-task-list ul input[type="checkbox"] {
  margin-left: 24px;
}

.markdown-body blockquote {
  border-left: 4px solid #dfe2e5;
  padding: 0 15px;
  color: #777777;
}
.markdown-body blockquote blockquote {
  padding-right: 0;
}
.markdown-body table {
  padding: 0;
  word-break: initial;
  width: 100%;
}
.markdown-body table tr {
  border: 1px solid #dfe2e5;
  margin: 0;
  padding: 0;
}
.markdown-body table tr:nth-child(2n),
.markdown-body thead {
  background-color: #f8f8f8;
}
.markdown-body table th {
  font-weight: bold;
  border: 1px solid #dfe2e5;
  border-bottom: 0;
  margin: 0;
  padding: 6px 13px;
}
.markdown-body table td {
  border: 1px solid #dfe2e5;
  margin: 0;
  padding: 6px 13px;
}
.markdown-body table th:first-child,
.markdown-body table td:first-child {
  margin-top: 0;
}
.markdown-body table th:last-child,
.markdown-body table td:last-child {
  margin-bottom: 0;
}

.markdown-body .contains-task-list {
  margin: 12.8px 0;
}
.markdown-body .task-list-item {
  list-style-type: none;
  margin-left: -1.3em;
  line-height: 1.9;
}
.markdown-body .task-list-item > input {
  list-style-type: none;
}
.markdown-body .task-list-item > input:checked {
  cursor: pointer;
}

.markdown-body code,
tt {
  border: 1px solid #e7eaed;
  background-color: #f8f8f8;
  color: #333333;
  border-radius: 3px;
  padding: 0;
  padding: 2px 4px 0px 4px;
  font-size: 0.9em;
}

.markdown-body .CodeMirror-lines {
  padding-left: 4px;
}

/* this is to display what kind of language it is */
.markdown-body .code-tooltip {
  box-shadow: 0 1px 1px 0 rgba(0, 28, 36, 0.3);
  border-top: 1px solid #eef2f2;
  list-style-type: none;
}

.markdown-body code {
  background-color: #f3f4f4;
  padding: 0 2px 0 2px;
}

.markdown-body .md-fences {
  margin-bottom: 15px;
  margin-top: 15px;
  padding-top: 8px;
  padding-bottom: 6px;
}

.markdown-body pre {
  border: 1px solid #e7eaed;
  background-color: #ffffff;
  border-radius: 3px;
  padding: 2px 4px 0px 4px;
  padding-left: 10px;
}
.markdown-body pre > code {
  all: unset; /* 清除所有默认样式 */
  background-color: #ffffff;
  border-radius: 3px;
  font-family: monospace;
}

.markdown-body pre > code > span {
  background-color: #ffffff;
}

.markdown-body img {
  max-width: 100%;
  height: auto;
}

@media print {
  html {
    font-size: 13px;
  }
  pre {
    page-break-inside: avoid;
    word-wrap: break-word;
  }
}

.md-fences {
  background-color: #f8f8f8;
}
#write pre.md-meta-block {
  padding: 1rem;
  font-size: 85%;
  line-height: 1.45;
  background-color: #f7f7f7;
  border: 0;
  border-radius: 3px;
  color: #777777;
  margin-top: 0 !important;
}

.mathjax-block > .code-tooltip {
  bottom: 0.375rem;
}

.md-mathjax-midline {
  background: #fafafa;
}

#write > h3.md-focus:before {
  left: -1.5625rem;
  top: 0.375rem;
}
#write > h4.md-focus:before {
  left: -1.5625rem;
  top: 0.285714286rem;
}
#write > h5.md-focus:before {
  left: -1.5625rem;
  top: 0.285714286rem;
}
#write > h6.md-focus:before {
  left: -1.5625rem;
  top: 0.285714286rem;
}
.md-image > .md-meta {
  /*border: 1px solid #ddd;*/
  border-radius: 3px;
  padding: 2px 0px 0px 4px;
  font-size: 0.9em;
  color: inherit;
}

.md-tag {
  color: #a7a7a7;
  opacity: 1;
}

.md-toc {
  margin-top: 20px;
  padding-bottom: 20px;
}

.sidebar-tabs {
  border-bottom: none;
}

#typora-quick-open {
  border: 1px solid #ddd;
  background-color: #f8f8f8;
}

#typora-quick-open-item {
  background-color: #fafafa;
  border-color: #fefefe #e5e5e5 #e5e5e5 #eee;
  border-style: solid;
  border-width: 1px;
}

/** focus mode */
.on-focus-mode blockquote {
  border-left-color: rgba(85, 85, 85, 0.12);
}

header,
.context-menu,
.megamenu-content,
footer {
  font-family: "Segoe UI", "Arial", sans-serif;
}

.file-node-content:hover .file-node-icon,
.file-node-content:hover .file-node-open-state {
  visibility: visible;
}

.mac-seamless-mode #typora-sidebar {
  background-color: #fafafa;
  background-color: var(--side-bar-bg-color);
}

.mac-os #write {
  caret-color: AccentColor;
}

.md-lang {
  color: #b4654d;
}

/*.html-for-mac {
    --item-hover-bg-color: #E6F0FE;
}*/

#md-notification .btn {
  border: 0;
}

.dropdown-menu .divider {
  border-color: #e5e5e5;
  opacity: 0.4;
}

.ty-preferences .window-content {
  background-color: #fafafa;
}

.ty-preferences .nav-group-item.active {
  color: white;
  background: #999;
}

.menu-item-container a.menu-style-btn {
  background-color: #f5f8fa;
  background-image: linear-gradient(
    180deg,
    hsla(0, 0%, 100%, 0.8),
    hsla(0, 0%, 100%, 0)
  );
}
